const BOARD_CREATE = "CREATE";
const BOARD_DELETE = "DELETE";
const BOARD_READ = "READ";
const BOARD_LIST = "LIST";

const initialState = {
  total: 2,
  boards: [
    {
      brdnum: 1,
      writer: "Kyo",
      title: "Hello",
      desc: "ÏïàÎÖïÌïòÏÑ∏Ïöî~",
      date: new Date(),
    },
    {
      brdnum: 2,
      writer: "Roh",
      title: "Good bye",
      desc: "üòÅ",
      date: new Date(),
    },
  ],
  selectedBoard: {},
};

// reducer
// CRUD Ï≤òÎ¶¨Î•º reducer Î©îÏÜåÎìúÏóêÏÑú Î™®Îëê Ï≤òÎ¶¨
export default function board_reducer(state = initialState, action) {
  let boards = state.boards;

  console.log("state\n", state);
  console.log("action\n", action);

  switch (action.type) {
    case BOARD_CREATE: {
      let data = action.data;
      let total = state.total;

      // ÏÉàÎ°úÏö¥ ÏÇΩÏûÖ
      if (!data.brdnum) {
        return {
          total: total + 1,
          boards: boards.concat({ brdnum: ++total, ...data, date: new Date() }),
          selectedBoard: {},
        };
      }

      return {
        ...state,
        boards: boards.map((row) =>
          data.brdnum === row.brdnum ? { ...data } : row
        ),
        selectedBoard: {},
      };
    }
    case BOARD_DELETE: {
      let total = state.total;
      let initNum = 1;
      // ÏÇ≠Ï†úÌï† row ÎπºÍ≥† Îã§Ïãú Î∞∞Ïó¥Î°ú ÎßåÎì§Í∏∞
      return {
        total: total - 1,
        boards: boards
          .filter((row) => row.brdnum !== action.brdnum)
          .map((row) => {
            row.brdnum = initNum++;
            if (initNum === total) initNum = 1;
            return row;
          }),
        selectedBoard: {},
      };
    }
    case BOARD_READ:
      return {
        ...state,
        selectedBoard: boards.find((row) => row.brdnum === action.brdnum),
      };
    default:
      return state;
  }
}
