import React from "react";
import { connect } from "react-redux";

import { board_delete } from "../../../redux/action";
import { DefaultButton } from "../../modules/Button";
import styled from "styled-components";

const handleRemove = (history, brdnum) => {
  board_delete(brdnum);
  history.push("/Board");
};
function BoardDetail(props) {
  const { history, selectedBoard } = props; // mapReduxStateToReactProps Î©îÏÜåÎìú Ïù¥Ïö©Ìï¥ÏÑú reducerÏùò state Í∞ÄÏ†∏Ïò§Í∏∞

  return (
    <MainContainer>
      <BoardContainer>
        <Title>
          <div>{selectedBoard.title}</div>
        </Title>
        <Writer>{selectedBoard.writer}</Writer>
        <Desc>{selectedBoard.desc}</Desc>
      </BoardContainer>
      <BottomContainer>
        <EditButton to="Write">ÏàòÏ†ïÌïòÍ∏∞</EditButton>
        <DeleteButton
          onClick={() => handleRemove(history, selectedBoard.brdnum)}
        >
          ÏÇ≠Ï†úÌïòÍ∏∞
        </DeleteButton>
      </BottomContainer>
    </MainContainer>
  );
}

const MainContainer = styled.main`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90%;
`;

const BoardContainer = styled.div`
  height: 90%;
  width: 90%;
`;

const BottomContainer = styled.footer`
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 10%;
  width: 90%;
  border-top: 1px solid #bdc3c7;
`;

const Title = styled.div`
  display: flex;
  align-items: center;
  height: 60px;
  font-size: 30px;
  font-family: "KOTRA_BOLD-Bold", sans-serif;
`;
const Writer = styled.div`
  border-bottom: 1px solid #bdc3c7;
  font-size: 20px;
  font-family: "KOTRA_BOLD-Bold", sans-serif;
`;
const Desc = styled.article`
  height: "100%";
  margin-top: "5%";
  font-family: "NanumBarunGothic", sans-serif;
`;

const EditButton = styled(DefaultButton)`
  background-color: #0984e3;
`;

const DeleteButton = styled(DefaultButton)`
  background-color: #c0392b;
`;

// ReduecerÏùò state.boardsÎ•º boardsÎ°ú Î∞õÏïÑÏ£ºÍ∏∞
function mapReduxStateToReactProps(state) {
  console.log("BoardDetail mapReduxStateToReactProps\n", state);
  return { selectedBoard: state.selectedBoard };
}

export default connect(mapReduxStateToReactProps)(BoardDetail);
